/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import Products from "../../../products"
import { usePortal } from "Hooks"
import { useSearchState } from "../../_state/hooks"
import { useItemsState } from "../../../items/_state/hooks"

const SearchModal = wp.element.lazy(() =>
  import(/* webpackChunkName: 'SearchModal-public' */ "../modal")
)

function SearchItems() {
  const searchState = useSearchState()
  const itemsState = useItemsState()

  return itemsState.settings.dropzonePayload ? (
    usePortal(
      <>
        {itemsState.searchTerm ? (
          <Products settings={itemsState.settings} />
        ) : null}
      </>,
      document.querySelector(itemsState.settings.dropzonePayload)
    )
  ) : (
    <SearchModal
      payload={itemsState.payload}
      searchTerm={searchState.searchTerm}
      settings={itemsState.settings}
    />
  )
}

export default SearchItems
