/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { mq } from "Common/css"
import PaginationItemsMap from "./map"
import { useItemsState } from "../../items/_state/hooks"
import { removeSkelly } from "Utils"
import has from "lodash/has"

function PaginationItems(props) {
  const itemsState = useItemsState()

  if (has(props, "payload")) {
    var payload = props.payload
  } else {
    var payload = itemsState.payload
  }

  if (has(props, "isLoading")) {
    var isLoading = props.isLoading
  } else {
    var isLoading = itemsState.isLoading
  }

  // if (has(props, 'settings')) {
  // 	var settings = props.settings
  // } else {
  // 	var settings = itemsState.settings
  // }

  var settings = itemsState.settings

  if (has(props, "element")) {
    var element = props.element
  } else {
    var element = itemsState.element
  }

  const PaginationItemsCSS = css`
    display: ${settings.carousel && shopwp.misc.isPro ? "block" : "grid"};
    grid-template-columns: repeat(
      ${payload.length <= 1 ? 1 : settings.itemsPerRow},
      1fr
    );
    grid-column-gap: ${settings.gridColumnGap
      ? settings.gridColumnGap
      : "20px"};
    grid-row-gap: ${settings.isSingleComponent ? "0px" : "40px"};
    padding: 0;
    transition: opacity 0.3s ease;
    opacity: ${isLoading ? 0.4 : 1};

    ${mq("medium")} {
      grid-template-columns: ${payload.length === 1
        ? "repeat(1, 1fr)"
        : "repeat(2, 1fr)"};
    }

    ${mq("small")} {
      grid-template-columns: ${"repeat(" + settings.mobileColumns + ", 1fr)"};
    }
  `

  const PaginationItemsContainerCSS = css`
    max-width: ${settings.containerWidth};
    margin: 0 auto;
    padding: 0;

    ${mq("medium")} {
      padding: 0 15px;
    }
  `

  function onEmptyRender() {
    removeSkelly(element)
  }

  return payload ? (
    <section className="wps-items-wrapper" css={PaginationItemsContainerCSS}>
      <section className="wps-items wps-items-list" css={PaginationItemsCSS}>
        <PaginationItemsMap
          defaultCarouselSettings={itemsState.defaultCarouselSettings}
          payload={payload}
          settings={settings}
          element={element}
          id={itemsState.id}
        >
          {props.children}
        </PaginationItemsMap>
      </section>
    </section>
  ) : null
}

export default PaginationItems
