import defaults from "./defaults"
import { mergeDefaults } from "Common/state"

function ItemsInitialState({
  element,
  settings,
  id,
  queryParams = false,
  queryType = "products",
  componentType = "products",
  payload = false,
  isModal = false,
  isLoading = false,
  customTotalShown = false,
  afterLoading = false,
  beforeLoading = false,
  initialFetch = true,
}) {
  console.log("ItemsInitialState before settings", settings)
  console.log(
    "ItemsInitialState before defaults",
    defaults,
    defaults[componentType]
  )

  settings = mergeDefaults(settings, defaults[componentType])

  console.log("ItemsInitialState final settings", settings)

  if (isModal) {
    settings.carousel = false
  }

  const finalQueryParams = {
    query: queryParams?.query
      ? queryParams.query
      : settings?.query
      ? settings.query
      : false,
    sortKey: queryParams?.sortKey
      ? queryParams.sortKey.toUpperCase()
      : settings?.sortBy
      ? settings.sortBy.toUpperCase()
      : "TITLE",
    reverse: queryParams?.reverse
      ? queryParams.reverse
      : settings?.reverse
      ? settings.reverse
      : false,
    first: queryParams?.first
      ? queryParams.first
      : settings?.limit
      ? settings.limit
      : settings?.pageSize
      ? settings.pageSize
      : 10,
    collection_titles: settings.collection,
  }

  var itemsState = {
    id: id,
    componentType: componentType,
    settings: settings,
    isModal: isModal,
    element: element,
    payload: payload,
    queryType: settings.collection ? "collectionProducts" : queryType, // Can be either products, collections, or collectionProducts
    queryParams: finalQueryParams,
    originalParams: finalQueryParams,
    productQueryParams: {
      query: settings.products?.query ? settings.products.query : false,
      sortKey: settings.products?.sortBy ? settings.products.sortBy : false,
      reverse: settings.products?.reverse ? settings.products.reverse : false,
      first: settings.products?.pageSize ? settings.products.pageSize : false,
    },
    cursor: false,
    totalShown: customTotalShown ? customTotalShown : 0,
    noResultsText: settings?.noResultsText
      ? settings.noResultsText
      : wp.i18n.__("No items left", "shopwp"),
    isLoading: isLoading,
    isBootstrapping: true,
    isFetchingNew: initialFetch ? true : false,
    isFetchingNext: false,
    nextQueryId: false,
    hasMoreItems: false,
    hasNextPage: false,
    hasPreviousPage: false,
    notice: false,
    lastQuery: settings?.query ? settings.query : false,
    afterLoading: afterLoading,
    beforeLoading: beforeLoading,
    htmlTemplate: settings?.htmlTemplate ? settings.htmlTemplate : false,
    htmlTemplateData: false,
    defaultCarouselSettings: {
      carouselPrevArrow: settings.carouselPrevArrow,
      carouselNextArrow: settings.carouselNextArrow,
      carouselDots: settings.carouselDots,
      carouselInfinite: settings.carouselInfinite,
      carouselSpeed: settings.carouselSpeed,
      carouselSlidesToShow: settings.carouselSlidesToShow,
      carouselSlidesToScroll: settings.carouselSlidesToScroll,
    },
    withProducts:
      settings.single && queryType === "collections"
        ? true
        : settings.collection && settings.queryType === "products"
        ? true
        : false, // Used with collections queries
  }

  return itemsState
}

export { ItemsInitialState }
