import { get, post } from '../request'

function getSyncingStatus() {
	return post('syncing/status')
}

function setSyncingIndicator(data = {}) {
	return post('syncing/indicator', data)
}

function syncProductDetailPages() {
	return post('syncing/product_detail_pages')
}

function syncRemove() {
	return post('syncing/remove')
}

function syncExpire() {
	return post('syncing/expire')
}

function syncOn() {
	return setSyncingIndicator({
		syncing: true,
	})
}

function syncOff() {
	return setSyncingIndicator({
		syncing: false,
	})
}

function updateSettings(settings, flushRewriteRules = false) {
	return post('settings', {
		settings: settings,
		flushRewriteRules: flushRewriteRules,
	})
}

function removeConnection() {
	return post('connection/delete')
}

export {
	getSyncingStatus,
	setSyncingIndicator,
	syncProductDetailPages,
	syncRemove,
	syncExpire,
	syncOn,
	syncOff,
	updateSettings,
	removeConnection,
}
