import isError from "lodash/isError"
import has from "lodash/has"

function get(endpoint) {
  return request(
    "get",
    shopwp.api.restUrl + shopwp.api.namespace + "/" + endpoint
  )
}

function post(endpoint, data = {}) {
  return request(
    "post",
    shopwp.api.restUrl + shopwp.api.namespace + "/" + endpoint,
    data
  )
}

function del(endpoint, data = {}) {
  return request(
    "delete",
    shopwp.api.restUrl + shopwp.api.namespace + "/" + endpoint,
    data
  )
}

function getRestErrorContents(error) {
  if (isError(error)) {
    return {
      statusCode: "unknown",
      message: error.message,
      action_name: error.name,
    }
  } else {
    return {
      statusCode: error.response.status,
      message: error.response.data.message,
      action_name: error.response.data.code,
    }
  }
}

function request(method, endpoint, data = {}) {
  return new Promise((resolve, reject) => {
    let options = {
      method: method,
      headers: {
        "X-WP-Nonce": shopwp.api.nonce,
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    }

    if (method !== "get") {
      options.body = JSON.stringify(data)
    }

    fetch(endpoint, options)
      .then((response) => {
        return response.json()
      })
      .then((resp) => {
        if (has(resp, "code") && has(resp, "message")) {
          reject({
            statusCode: resp.data.status,
            message: resp.message,
            action_name: resp.code,
          })
        }

        resolve(resp)
      })
      .catch((error) => {
        reject(getRestErrorContents(error))
      })
  })
}

export { get, post, del, getRestErrorContents }
