import has from "lodash/has"
import { removeSkelly } from "Utils"

const Carousel = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Carousel-public' */ "../../carousel")
)

function createUniqueKey(item, id) {
  return btoa(item?.node ? item.node.id : item.id + "-" + id)
}

function PaginationItemsMap({
  children,
  payload,
  settings,
  defaultCarouselSettings,
  element,
  id,
}) {
  const { Suspense } = wp.element

  return settings.carousel && payload.length && shopwp.misc.isPro ? (
    <Suspense fallback={`Loading ${settings.type} ...`}>
      <Carousel defaultSettings={defaultCarouselSettings} element={element}>
        {payload.map((item) => {
          return wp.element.cloneElement(children, {
            key: has(item, "node") ? item.node.id : item.id,
            payload: has(item, "node") ? item.node : item,
            settings: settings,
            id: id,
          })
        })}
      </Carousel>
    </Suspense>
  ) : (
    <PagMap
      childrenItems={children}
      payload={payload}
      settings={settings}
      element={element}
      id={id}
    />
  )
}

function PagMap(props) {
  const { useEffect } = wp.element

  useEffect(() => {
    if (props.element) {
      removeSkelly(props.element)
    }
  }, [])

  return props.payload.map((item) => {
    return wp.element.cloneElement(props.childrenItems, {
      ...props,
      id: props.id,
      key: createUniqueKey(item, props.id),
      payload: has(item, "node") ? item.node : item,
      settings: props.settings,
      element: props.element,
    })
  })
}

export default PaginationItemsMap
