import update from "immutability-helper"
import has from "lodash/has"
import { updatePayload, rSet } from "Utils"

function ItemsReducer(state, action) {
  switch (action.type) {
    case "SET_IS_LOADING": {
      return rSet("isLoading", action, state)
    }

    case "SET_IS_FETCHING_NEXT": {
      return rSet("isFetchingNext", action, state)
    }

    case "SET_IS_FETCHING_NEW": {
      return rSet("isFetchingNew", action, state)
    }

    case "SET_IS_BOOTSTRAPPING": {
      return rSet("isBootstrapping", action, state)
    }

    case "SET_TOTAL_SHOWN": {
      return rSet("totalShown", action, state)
    }

    case "UPDATE_HTML_TEMPLATE": {
      return rSet("htmlTemplate", action, state)
    }

    case "UPDATE_HTML_TEMPLATE_DATA": {
      return rSet("htmlTemplateData", action, state)
    }

    case "SET_SETTINGS": {
      return rSet("settings", action, state)
    }

    case "SET_HAS_NEXT_PAGE": {
      return rSet("hasNextPage", action, state)
    }

    case "SET_HAS_PREVIOUS_PAGE": {
      return rSet("hasPreviousPage", action, state)
    }

    case "SET_CURSOR": {
      return rSet("cursor", action, state)
    }

    case "SET_QUERY_TYPE": {
      return rSet("queryType", action, state)
    }

    case "SET_QUERY_PARAMS": {
      return rSet("queryParams", action, state)
    }

    case "SET_NOTICE": {
      return rSet("notice", action, state)
    }

    case "SET_WITH_PRODUCTS": {
      return rSet("withProducts", action, state)
    }

    case "RESET_QUERY_PARAMS": {
      var newParams = update(state.queryParams, {
        $set: state.originalParams,
      })

      return {
        ...state,
        queryParams: newParams,
      }
    }

    case "UPDATE_TOTAL_SHOWN": {
      const newTotal = action.payload + state.totalShown

      return {
        ...state,
        totalShown: update(state.totalShown, { $set: newTotal }),
      }
    }

    case "UPDATE_PAYLOAD": {
      if (!action.payload) {
        return {
          ...state,
        }
      }

      if (!has(action.payload, "replace")) {
        action.payload.replace = true
      }

      const newItemsState = updatePayload(
        state,
        action.payload.items,
        action.payload.replace
      )

      wp.hooks.doAction("on.afterPayloadUpdate", newItemsState)

      return newItemsState
    }

    default: {
      throw new Error(`Unhandled action type: ${action.type} in ItemsReducer`)
    }
  }
}

export { ItemsReducer }
