/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { ErrorBoundary } from "react-error-boundary"
import { isShowingComponent } from "Common/components"
import ErrorFallback from "../../../error-fallback"
import { useProductState } from "../_state/hooks"
import { useItemsState } from "../../../items/_state/hooks"
import BuyButtonSkeleton from "../buy-button/skeleton"

const Notice = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Notice-public' */ "../../../notice")
)

const ProductCustomTemplate = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductCustomTemplate-public' */ "../template")
)

const ProductTitle = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductTitle-public' */ "../title")
)

const ProductPricing = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductPricing-public' */ "../pricing")
)

const ProductDescription = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductDescription-public' */ "../description")
)

const ProductImages = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductImages-public' */ "../images")
)

const ProductBuyButton = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductBuyButton-public' */ "../buy-button")
)

const ProductModal = wp.element.lazy(() =>
  import(/* webpackChunkName: 'ProductModal-public' */ "../buy-button/modal")
)

function ProductWrapper() {
  const { Suspense } = wp.element
  const itemsState = useItemsState()
  const productState = useProductState()

  const ProductWrapperCSS = css`
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    min-width: 0;
    max-width: ${itemsState.payload.length === 1 && !itemsState.isModal
      ? "350px"
      : "100%"};

    > div:nth-last-of-type(2) {
      flex: ${isAlignHeight() ? "1" : "none"};
    }
  `

  function isAlignHeight() {
    console.log("isAlignHeight shopwp", shopwp)
    console.log("isAlignHeight itemsState", itemsState)
    return shopwp.general.alignHeight || itemsState.settings.alignHeight
  }

  return (
    <div
      css={ProductWrapperCSS}
      className="wps-item"
      aria-label="Product"
      data-wpshopify-is-available-for-sale={
        productState.payload.availableForSale
      }
      data-wpshopify-is-on-sale={productState.isOnSale}
    >
      {itemsState.settings.htmlTemplate ||
      itemsState.settings.htmlTemplateData ? (
        <ProductCustomTemplate
          customHtmlData={itemsState.settings.htmlTemplateData}
          payload={productState.payload}
        />
      ) : (
        <>
          <ErrorBoundary FallbackComponent={ErrorFallback}>
            <Suspense fallback={false}>
              {isShowingComponent(itemsState.settings, "images") ? (
                <ProductImages />
              ) : null}
            </Suspense>
          </ErrorBoundary>

          <ErrorBoundary FallbackComponent={ErrorFallback}>
            <Suspense fallback={false}>
              {isShowingComponent(itemsState.settings, "title") ? (
                <ProductTitle />
              ) : null}
            </Suspense>
          </ErrorBoundary>

          <ErrorBoundary FallbackComponent={ErrorFallback}>
            <Suspense fallback={false}>
              {isShowingComponent(itemsState.settings, "pricing") ? (
                <ProductPricing />
              ) : null}
            </Suspense>
          </ErrorBoundary>

          <ErrorBoundary FallbackComponent={ErrorFallback}>
            <Suspense fallback={false}>
              {isShowingComponent(itemsState.settings, "description") ? (
                <ProductDescription />
              ) : null}
            </Suspense>
          </ErrorBoundary>

          <ErrorBoundary FallbackComponent={ErrorFallback}>
            <Suspense fallback={<BuyButtonSkeleton />}>
              {isShowingComponent(itemsState.settings, "buy-button") ? (
                <ProductBuyButton />
              ) : null}
            </Suspense>
          </ErrorBoundary>
        </>
      )}

      {productState.isModalOpen && shopwp.misc.isPro && productState.payload ? (
        <Suspense fallback={false}>
          <ProductModal payload={productState.payload} />
        </Suspense>
      ) : null}

      {productState.notice ? (
        <Notice status="warning">{productState.notice.message}</Notice>
      ) : null}
    </div>
  )
}

export default ProductWrapper
