import {
  maybeAlterErrorMessage,
  isWordPressError,
  getWordPressErrorMessage,
} from "../errors"
import to from "await-to-js"
import isEmpty from "lodash/isEmpty"
import has from "lodash/has"
import isArray from "lodash/isArray"
import {
  getProducts,
  getProductById,
  getProductsByCollections,
} from "../internal/products"

function fetchProducts(queryParams, shopState, cursor = false) {
  return new Promise(async (resolve, reject) => {
    if (!queryParams) {
      console.error(
        "ShopWP error: Uh oh, no query parameters were passed for fetchProducts. Please clear your browser cache and try again."
      )

      reject({
        type: "error",
        message: wp.i18n.__(
          "Uh oh, no query parameters were passed for fetchProducts. Please clear your browser cache and try again.",
          "shopwp"
        ),
      })
    }

    if (queryParams?.sortKey) {
      queryParams.sortKey = queryParams.sortKey.toUpperCase()
    }

    if (cursor) {
      queryParams.cursor = cursor
    } else {
      delete queryParams.cursor
    }

    const [resultsError, results] = await to(
      getProducts({
        queryParams: queryParams,
        shopState: shopState,
      })
    )

    if (resultsError) {
      reject({ type: "error", message: maybeAlterErrorMessage(resultsError) })
      return
    }

    resolve(results.data)
  })
}

function fetchProductsByCollections(
  queryParamsCollectionProducts,
  shopState,
  cursor = false
) {
  return new Promise(async (resolve, reject) => {
    if (!queryParamsCollectionProducts) {
      return reject({
        type: "error",
        message: wp.i18n.__(
          "Uh oh, no query parameters were passed for fetchProductsByCollections. Please clear your browser cache and try again.",
          "shopwp"
        ),
      })
    }

    if (queryParamsCollectionProducts?.sortKey) {
      queryParamsCollectionProducts.sortKey =
        queryParamsCollectionProducts.sortKey.toUpperCase()
    }

    if (queryParamsCollectionProducts?.first) {
      queryParamsCollectionProducts.first = parseInt(
        queryParamsCollectionProducts.first,
        10
      )
    }

    if (cursor) {
      queryParamsCollectionProducts.cursor = cursor
    } else {
      delete queryParamsCollectionProducts.cursor
    }

    const [resultsError, results] = await to(
      getProductsByCollections({
        queryParamsCollectionProducts: queryParamsCollectionProducts,
        shopState: shopState,
      })
    )

    if (resultsError) {
      reject({ type: "error", message: maybeAlterErrorMessage(resultsError) })
      return
    }

    if (isWordPressError(results)) {
      return reject({
        type: "error",
        message: getWordPressErrorMessage(results),
      })
    }

    if (isEmpty(results.data)) {
      resolve(results.data)
      return
    }

    if (isArray(results.data)) {
      var nonFalseyData = results.data.filter((v) => !!v)
    } else {
      var nonFalseyData = results.data.nodes.filter((v) => !!v)
    }

    if (!isEmpty(nonFalseyData)) {
      if (has(nonFalseyData[0], "products")) {
        resolve(nonFalseyData[0].products)
      } else {
        resolve(false)
      }
    } else {
      resolve(nonFalseyData)
    }
  })
}

function fetchProductById(storefrontId) {
  return new Promise(async (resolve, reject) => {
    const [resultsError, results] = await to(
      getProductById({
        id: storefrontId,
      })
    )

    if (resultsError) {
      reject({ type: "error", message: maybeAlterErrorMessage(resultsError) })
      return
    }

    resolve(results.data)
  })
}

export { fetchProducts, fetchProductsByCollections, fetchProductById }
