import to from "await-to-js"
import { isArray } from "lodash"
import {
  getCart,
  createCart,
  addLineItems,
  updateLineItems,
  removeLineItems,
  updateCartAttributes,
  maybeHandleApiError,
  applyDiscount,
  updateNote,
} from "Api"
import { findTotalCartQuantities } from "Common/cart"
import { checkoutRedirect } from "Common/checkout"

function hasDiscount(cartData) {
  if (!cartData.discountCodes) {
    return false
  }

  var appliedDiscounts = cartData.discountCodes.filter(
    (discount) => discount.applicable
  )

  if (!appliedDiscounts.length) {
    return false
  }

  return appliedDiscounts[0].code
}

async function updateLines(
  shopState,
  cartDispatch,
  shopDispatch,
  lineItem,
  newQuantity
) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [updateError, response] = await to(
    updateLineItems({
      cartId: shopState.cartData.id,
      lines: {
        id: lineItem.id,
        quantity: newQuantity,
      },
      language: shopState.language,
      country: shopState.country,
    })
  )

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(updateError, response, cartDispatch)) {
    return
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })

  if (!response.data.lines.edges.length) {
    var isEmpty = true
  } else {
    var isEmpty = false
  }

  cartDispatch({
    type: "SET_IS_CART_EMPTY",
    payload: isEmpty,
  })

  shopDispatch({
    type: "SET_TOTAL_LINE_ITEMS",
    payload: findTotalCartQuantities(response),
  })
}

async function removeLines(lineItemIds, shopState, cartDispatch, shopDispatch) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [error, response] = await to(
    removeLineItems({
      lineIds: lineItemIds,
      cartId: shopState.cartData.id,
      language: shopState.language,
      country: shopState.country,
    })
  )

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(error, response, cartDispatch)) {
    return
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })

  shopDispatch({
    type: "SET_TOTAL_LINE_ITEMS",
    payload: findTotalCartQuantities(response),
  })

  if (response.data.lines.edges.length) {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: false,
    })
  } else {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: true,
    })
  }
}

async function addLines(data, cartDispatch, shopDispatch) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [addError, response] = await to(addLineItems(data))

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(addError, response, cartDispatch)) {
    return
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })

  shopDispatch({
    type: "SET_TOTAL_LINE_ITEMS",
    payload: findTotalCartQuantities(response),
  })

  if (response.data.lines.edges.length) {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: false,
    })
  } else {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: true,
    })
  }
}

async function updateAttrs(data, shopState, cartDispatch, shopDispatch) {
  if (isArray(data)) {
    var [attrs, options] = data
  } else {
    var attrs = data
    var options = false
  }

  if (options && options.openCart) {
    shopDispatch({ type: "TOGGLE_CART", payload: true })
  }

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [addError, response] = await to(
    updateCartAttributes({
      attributes: attrs,
      cartId: shopState.cartData.id,
      language: shopState.language,
      country: shopState.country,
    })
  )

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(addError, response, cartDispatch)) {
    return
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })
}

async function getExistingCart(
  cartId,
  cartState,
  shopState,
  cartDispatch,
  shopDispatch
) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [getCartError, response] = await to(getCart(cartId))

  wp.hooks.doAction("on.cartLoad", cartState)

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(getCartError, response, cartDispatch)) {
    localStorage.removeItem("shopwp-cart-id")

    if (response.data.includes("No cart data found")) {
      createNewCart(cartState, shopState, cartDispatch, shopDispatch)
    }

    return
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })

  var discount = hasDiscount(response.data)

  if (discount) {
    cartDispatch({
      type: "SET_DISCOUNT_CODE",
      payload: discount,
    })
  }

  shopDispatch({
    type: "SET_TOTAL_LINE_ITEMS",
    payload: findTotalCartQuantities(response),
  })

  if (response.data.lines.edges.length) {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: false,
    })
  } else {
    cartDispatch({
      type: "SET_IS_CART_EMPTY",
      payload: true,
    })
  }
}

async function createNewCart(cartState, shopState, cartDispatch, shopDispatch) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [createCartError, response] = await to(
    createCart(
      wp.hooks.applyFilters(
        "cart.createSettings",
        {
          language: shopState.language,
          country: shopState.country,
          note: "",
          buyerIdentity: {
            countryCode: shopState.country,
          },
        },
        cartState,
        shopState
      )
    )
  )

  wp.hooks.doAction("on.cartLoad", cartState)

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(createCartError, response, cartDispatch)) {
    shopDispatch({
      type: "SET_CART_DATA",
      payload: false,
    })
  } else {
    shopDispatch({
      type: "SET_CART_DATA",
      payload: response.data,
    })

    localStorage.setItem("shopwp-cart-id", response.data.id)
  }
}

async function directCheckout(data, cartState, cartDispatch, shopState) {
  const [directCheckoutError, response] = await to(
    createCart(
      wp.hooks.applyFilters(
        "cart.directCheckoutSettings",
        {
          lines: data.lines,
          language: shopState.language,
          country: shopState.country,
          buyerIdentity: {
            countryCode: shopState.country,
          },
        },
        cartState,
        data
      )
    )
  )

  if (maybeHandleApiError(directCheckoutError, response, cartDispatch)) {
    return
  }

  // All good, let's go
  checkoutRedirect(response.data.checkoutUrl)
}

async function addDiscount(cartDispatch, shopState, discount, shopDispatch) {
  cartDispatch({
    type: "SET_NOTICE",
    payload: false,
  })

  if (!discount && discount !== "") {
    cartDispatch({
      type: "SET_NOTICE",
      payload: {
        type: "warning",
        message: wp.i18n.__(
          "No discount found! Please enter one below.",
          "shopwp"
        ),
      },
    })
    return
  }

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })
  cartDispatch({ type: "SET_IS_ADDING_DISCOUNT_CODE", payload: true })

  const [errorDiscount, response] = await to(
    applyDiscount({
      cartId: shopState.cartData.id,
      discountCodes: [discount],
      language: shopState.language,
      country: shopState.country,
    })
  )

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })
  cartDispatch({ type: "SET_IS_ADDING_DISCOUNT_CODE", payload: false })

  if (maybeHandleApiError(errorDiscount, response, cartDispatch)) {
    return
  }

  const addedDiscount = hasDiscount(response.data)

  cartDispatch({
    type: "SET_DISCOUNT_CODE",
    payload: addedDiscount ? discount : false,
  })

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })

  cartDispatch({
    type: "SET_NOTICE",
    payload: {
      type: "success",
      message: addedDiscount
        ? wp.i18n.__("Successfully added discount code")
        : wp.i18n.__("Successfully removed discount code"),
    },
  })
}

async function updateCartNote(
  note,
  shopState,
  cartDispatch,
  inputElement = false,
  shopDispatch
) {
  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: true })

  const [noteError, response] = await to(
    updateNote({
      cartId: shopState.cartData.id,
      note: note,
      language: shopState.language,
      country: shopState.country,
    })
  )

  shopDispatch({ type: "SET_IS_CART_UPDATING", payload: false })

  if (maybeHandleApiError(noteError, response, cartDispatch)) {
    return
  }

  wp.hooks.doAction("on.cartNoteChange", response.data)

  if (inputElement) {
    inputElement.current.focus()
  }

  shopDispatch({
    type: "SET_CART_DATA",
    payload: response.data,
  })
}

export {
  updateLines,
  removeLines,
  addLines,
  updateAttrs,
  getExistingCart,
  createNewCart,
  directCheckout,
  addDiscount,
  hasDiscount,
  updateCartNote,
}
