import CollectionProvider from "./_state/provider"
import { isShowingComponent } from "Common/components"
import { itemWidthClass } from "Utils"
import { useItemsState } from "../../items/_state/hooks"

const CollectionImage = wp.element.lazy(() =>
  import(/* webpackChunkName: 'CollectionImage-public' */ "./image")
)

const CollectionTitle = wp.element.lazy(() =>
  import(/* webpackChunkName: 'CollectionTitle-public' */ "./title")
)

const CollectionDescription = wp.element.lazy(() =>
  import(/* webpackChunkName: 'CollectionDescription-public' */ "./description")
)

const CollectionProducts = wp.element.lazy(() =>
  import(/* webpackChunkName: 'CollectionProducts-public' */ "./products")
)

function Collection(props) {
  const { Suspense } = wp.element
  const itemsState = useItemsState()

  return (
    <Suspense fallback="Loading collection...">
      <div
        className={`${itemWidthClass(
          itemsState.settings.itemsPerRow
        )} wps-item`}
      >
        <CollectionProvider itemsState={itemsState} payload={props.payload}>
          {isShowingComponent(itemsState.settings, "image") ? (
            <CollectionImage />
          ) : null}
          {isShowingComponent(itemsState.settings, "title") ? (
            <CollectionTitle />
          ) : null}
          {isShowingComponent(itemsState.settings, "description") ? (
            <CollectionDescription />
          ) : null}
          {isShowingComponent(itemsState.settings, "products") ? (
            <CollectionProducts />
          ) : null}
        </CollectionProvider>
      </div>
    </Suspense>
  )
}

export default Collection
