import {
  fetchProducts,
  fetchProductsByCollections,
  fetchCollections,
  getTemplate,
  queryOptionsNoRefetch,
  queryOptionsWithRefetch,
  maybeHandleApiError,
} from "Api"

import { removeSkelly } from "Utils"
import { useQuery } from "react-query"
import isEmpty from "lodash/isEmpty"
import has from "lodash/has"

function useGetNextItemsQuery(state, dispatch, shopState) {
  return useQuery(
    ["items::next", state.queryType, state.queryParams, state.cursor],
    () => {
      wp.hooks.doAction("on.beforePayloadUpdate", state)

      if (state.queryType === "collectionProducts") {
        return fetchProductsByCollections(
          state.queryParams,
          shopState,
          state.cursor
        )
      } else if (state.queryType === "collections") {
        return fetchCollections(state.queryParams, shopState, state.cursor)
      } else {
        return fetchProducts(state.queryParams, shopState, state.cursor)
      }
    },
    {
      enabled: state.isFetchingNext,
      retry: false,
      onError: (error) => {
        dispatch({ type: "SET_IS_FETCHING_NEXT", payload: false })
        dispatch({ type: "SET_IS_LOADING", payload: false })

        console.error("ShopWP Error", error)

        dispatch({
          type: "SET_NOTICE",
          payload: {
            type: "error",
            message: error,
          },
        })

        removeSkelly(state.element)
      },
      onSuccess: (newItems) => {
        dispatch({ type: "SET_IS_FETCHING_NEXT", payload: false })
        dispatch({ type: "SET_IS_LOADING", payload: false })

        if (newItems.pageInfo.hasNextPage && !newItems.edges.length) {
          return
        }

        var lastItem = newItems.edges[newItems.edges.length - 1]

        dispatch({
          type: "SET_CURSOR",
          payload: lastItem.cursor,
        })

        dispatch({
          type: "UPDATE_PAYLOAD",
          payload: {
            items: newItems.edges,
            replace: false,
          },
        })

        if (state.afterLoading) {
          state.afterLoading(newItems)
        }

        dispatch({
          type: "UPDATE_TOTAL_SHOWN",
          payload: newItems.edges.length,
        })

        dispatch({
          type: "SET_HAS_NEXT_PAGE",
          payload: newItems.pageInfo.hasNextPage,
        })

        dispatch({
          type: "SET_HAS_PREVIOUS_PAGE",
          payload: newItems.pageInfo.hasPreviousPage,
        })
      },
      ...queryOptionsNoRefetch,
    }
  )
}

function useGetItemsQuery(state, dispatch, shopState, isMounted) {
  return useQuery(
    ["items::new", state.queryType, state.queryParams],
    () => {
      wp.hooks.doAction("on.beforePayloadUpdate", state)

      if (state.beforeLoading) {
        state.beforeLoading()
      }

      dispatch({ type: "SET_IS_LOADING", payload: true })

      if (
        (state.queryType === "collectionProducts" && state.withProducts) ||
        (state.queryParams.ids && state.queryParams.ids.length)
      ) {
        return fetchProductsByCollections(state.queryParams, shopState)
      } else if (
        state.queryType === "collections" ||
        (state.queryType === "collectionProducts" && !state.withProducts)
      ) {
        return fetchCollections(
          state.queryParams,
          shopState,
          false,
          state.withProducts,
          state.productQueryParams
        )
      } else {
        return fetchProducts(state.queryParams, shopState)
      }
    },
    {
      enabled: state.isFetchingNew,
      retry: false,
      onError: (error) => {
        if (isMounted.current) {
          dispatch({ type: "SET_IS_LOADING", payload: false })
          dispatch({ type: "SET_IS_BOOTSTRAPPING", payload: false })
          dispatch({
            type: "SET_IS_FETCHING_NEW",
            payload: false,
          })

          console.error("ShopWP Error", error)

          removeSkelly(state.element)

          dispatch({
            type: "SET_NOTICE",
            payload: {
              type: "error",
              message: error.message,
            },
          })
        }
      },
      onSuccess: (newItems) => {
        var error = maybeHandleApiError(false, newItems, dispatch)

        if (error) {
          dispatch({
            type: "UPDATE_TOTAL_SHOWN",
            payload: 0,
          })

          dispatch({
            type: "UPDATE_PAYLOAD",
            payload: {
              items: [],
              replace: true,
            },
          })

          dispatch({
            type: "SET_NOTICE",
            payload: {
              type: "error",
              message: error,
            },
          })

          removeSkelly(state.element)

          return
        }

        if (isMounted.current) {
          if (
            !newItems ||
            isEmpty(newItems) ||
            (has(newItems, "edges") &&
              (!newItems.edges || !newItems.edges.length))
          ) {
            dispatch({
              type: "UPDATE_TOTAL_SHOWN",
              payload: 0,
            })

            dispatch({
              type: "UPDATE_PAYLOAD",
              payload: {
                items: [],
                replace: true,
              },
            })

            dispatch({
              type: "SET_NOTICE",
              payload: {
                type: "warning",
                message: state.settings.noResultsText,
              },
            })

            removeSkelly(state.element)
          } else {
            if (!newItems.edges) {
              dispatch({
                type: "SET_NOTICE",
                payload: {
                  type: "warning",
                  message: state.settings.noResultsText,
                },
              })

              return
            }
            console.log("newItems", newItems)
            var lastItem = newItems.edges[newItems.edges.length - 1]

            dispatch({
              type: "UPDATE_TOTAL_SHOWN",
              payload: newItems.edges.length,
            })

            dispatch({
              type: "SET_HAS_NEXT_PAGE",
              payload: newItems.pageInfo.hasNextPage,
            })

            dispatch({
              type: "SET_HAS_PREVIOUS_PAGE",
              payload: newItems.pageInfo.hasPreviousPage,
            })

            dispatch({
              type: "SET_CURSOR",
              payload: lastItem.cursor,
            })

            dispatch({
              type: "UPDATE_PAYLOAD",
              payload: {
                items: newItems.edges,
                replace: true,
              },
            })

            if (state.afterLoading) {
              state.afterLoading(newItems.edges)
            }
          }
        }

        // Removes the skeleton loader

        dispatch({ type: "SET_IS_LOADING", payload: false })
        dispatch({ type: "SET_IS_BOOTSTRAPPING", payload: false })

        dispatch({
          type: "SET_IS_FETCHING_NEW",
          payload: false,
        })
      },
      ...queryOptionsWithRefetch,
    }
  )
}

function useTemplateQuery(data = false, dispatch) {
  return useQuery(
    "templates",
    () => {
      return new Promise((resolve, reject) => {
        dispatch({
          type: "UPDATE_HTML_TEMPLATE_DATA",
          payload: data,
        })

        return resolve(data)
      })
    },
    {
      ...queryOptionsNoRefetch,
    }
  )
}

function useGetTemplateQuery(state, dispatch) {
  return useQuery(
    "templates",
    () => {
      return getTemplate(state.settings.htmlTemplate)
    },
    {
      enabled: !!state.htmlTemplate,
      onError: (error) => {
        dispatch({
          type: "SET_NOTICE",
          payload: {
            type: "error",
            message: error,
          },
        })

        dispatch({ type: "SET_IS_LOADING", payload: false })
        dispatch({ type: "SET_IS_BOOTSTRAPPING", payload: false })
      },
      onSuccess: (template) => {
        if (template?.success === false) {
          dispatch({ type: "SET_IS_LOADING", payload: false })

          dispatch({
            type: "SET_NOTICE",
            payload: {
              type: "error",
              message: template.data,
            },
          })

          dispatch({ type: "SET_IS_BOOTSTRAPPING", payload: false })

          return
        }

        dispatch({
          type: "UPDATE_HTML_TEMPLATE_DATA",
          payload: template.data,
        })
      },
      ...queryOptionsNoRefetch,
    }
  )
}

export { useGetItemsQuery, useGetTemplateQuery, useGetNextItemsQuery }
