import { rSet } from "Utils"

function TranslatorReducer(state, action) {
  switch (action.type) {
    case "SET_AVAILABLE_TRANSLATIONS": {
      return rSet("availableTranslations", action, state)
    }

    case "SET_SELECTED_COUNTRY": {
      return rSet("selectedCountry", action, state)
    }

    case "SET_SELECTED_LANGUAGE": {
      return rSet("selectedLanguage", action, state)
    }

    case "SET_NOTICE": {
      return rSet("notice", action, state)
    }

    case "SET_IS_LOADING": {
      return rSet("notice", action, state)
    }

    default: {
      throw new Error(
        `Unhandled action type: ${action.type} in TranslatorReducer`
      )
    }
  }
}

export default TranslatorReducer
