import has from "lodash/has"

function hasLink(settings) {
  if (!settings) {
    return false
  }

  if (settings.linkTo === "none") {
    return false
  }

  if (
    settings.linkTo === "shopify" ||
    settings.linkTo === "wordpress" ||
    settings.linkTo === "modal"
  ) {
    return true
  }
  console.log("hasLink shopwp", shopwp)
  console.log("hasLink settings", settings)
  if (shopwp.general.enableDefaultPages && !settings.isSingular) {
    return true
  }

  return false
}

function hasCustomButtonText(settings) {
  if (
    settings.addToCartButtonText != "Checkout" &&
    settings.addToCartButtonText != "Add to cart" &&
    settings.addToCartButtonText != "View product"
  ) {
    return true
  }

  return false
}

function getButtonText(settings, isDirectCheckout, linkWithBuyButton) {
  if (hasCustomButtonText(settings)) {
    return settings.addToCartButtonText
  }

  if (isDirectCheckout) {
    return wp.i18n.__("Checkout", "shopwp")
  }

  if (linkWithBuyButton) {
    return wp.i18n.__("Add to cart", "shopwp")
  }

  if (hasLink(settings)) {
    return wp.i18n.__("View product", "shopwp")
  }

  if (settings.addToCartButtonText === "View product") {
    return wp.i18n.__("View product", "shopwp")
  }

  if (settings.addToCartButtonText === "Checkout") {
    return wp.i18n.__("Checkout", "shopwp")
  }

  return wp.i18n.__("Add to cart", "shopwp")
}

function getShopifySingleLink(payload, type) {
  if (!payload.onlineStoreUrl) {
    return (
      "https://" +
      shopwp.connection.storefront.domain +
      "/" +
      type +
      "/" +
      encodeURI(payload.handle)
    )
  }

  return payload.onlineStoreUrl
}

function maybeAddSlashAtStart(string) {
  if (string.startsWith("http")) {
    return string
  }

  if (string.charAt(0) !== "/") {
    string = "/" + string
  }

  return string
}

function maybeAddSlashAtEnd(string) {
  if (string.charAt(string.length - 1) !== "/") {
    string = string + "/"
  }

  return string
}

function getWordPressSingleLink(payload) {
  let url = ""
  let itemHandle = payload.handle

  // Only products should contain this key on the payload obj
  if (has(payload, "productType")) {
    url = shopwp.general.urlProducts
  } else {
    url = shopwp.general.urlCollections
  }

  url = maybeAddSlashAtEnd(url)

  const finalURL = url + itemHandle + "/"

  return finalURL
}

function getItemLink(payload, type, linkTo) {
  if (linkTo === "none") {
    return false
  }

  if (linkTo === "shopify") {
    return getShopifySingleLink(payload, type)
  }

  if (linkTo === "wordpress") {
    return encodeURI(getWordPressSingleLink(payload))
  }
}

export {
  hasLink,
  getShopifySingleLink,
  getWordPressSingleLink,
  getItemLink,
  hasCustomButtonText,
  getButtonText,
}
