/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { getLocalizations } from "Api"
import { useTranslatorState, useTranslatorDispatch } from "../_state/hooks"
import { usePortal } from "Hooks"
import { removeSkelly } from "Utils"

const Notice = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Notice-public' */ "../../notice")
)

const TranslatorCountries = wp.element.lazy(() =>
  import(/* webpackChunkName: 'TranslatorCountries-public' */ "../countries")
)

const TranslatorLanguages = wp.element.lazy(() =>
  import(/* webpackChunkName: 'TranslatorLanguages-public' */ "../languages")
)

function TranslatorWrapper() {
  const { useEffect, Suspense } = wp.element
  const translatorState = useTranslatorState()
  const translatorDispatch = useTranslatorDispatch()

  useEffect(() => {
    removeSkelly(translatorState.element)
    getLocalizations(translatorDispatch)
  }, [])

  const TranslatorWrapperCSS = css`
    display: flex;
  `

  return usePortal(
    <div css={TranslatorWrapperCSS}>
      <Suspense fallback={false}>
        {translatorState.notice?.type ? (
          <Notice status={translatorState.notice.type}>
            {translatorState.notice.message}
          </Notice>
        ) : (
          <>
            <TranslatorCountries
              translatorState={translatorState}
              translatorDispatch={translatorDispatch}
            />
            {translatorState.selectedCountry &&
            translatorState.selectedCountry.value.availableLanguages &&
            translatorState.selectedCountry.value.availableLanguages.length >
              1 ? (
              <TranslatorLanguages
                translatorState={translatorState}
                translatorDispatch={translatorDispatch}
              />
            ) : null}
          </>
        )}
      </Suspense>
    </div>,
    translatorState.element
  )
}

export default TranslatorWrapper
