import defaults from "./defaults"
import { mergeDefaults } from "Common/state"

function CartInitialState(props) {
  let settings = mergeDefaults(props.settings, defaults)

  return {
    settings: settings,
    isCheckingOut: false,
    isCartOpen: false,
    termsAccepted: false,
    isCartEmpty: true,
    buttons: props.element,
    notice: false,
    discountCode: "",
    total: 0,
    buildNewCheckout: false,
    beforeDiscountTotal: false,
    percentageOff: false,
    amountOff: false,
    productsVisible: false,
    customAttributes: [],
    note: false,
    title: settings.cartTitle,
    checkoutText: settings.checkoutText,
    isRemovingDiscountCode: false,
    isAddingDiscountCode: false,
    isAddingLineItems: false,
  }
}

export default CartInitialState
