function ShopInitialState(props) {
  return {
    country: props.country
      ? props.country
      : shopwp.general.countryCode
      ? shopwp.general.countryCode
      : "US",
    language: props.language
      ? props.language
      : shopwp.general.languageCode
      ? shopwp.general.languageCode
      : "EN",
    currencyCode: props.currency
      ? props.currency
      : shopwp.general.currencyCode
      ? shopwp.general.currencyCode
      : "USD",
    isCartOpen: false,
    cartData: false,
    totalLineItems: 0,
    isCartUpdating: true,
  }
}

export default ShopInitialState
