/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { usePortal } from "Hooks"
import { buttonCSS } from "Common/css"
import { InView } from "react-intersection-observer"
import { useItemsState, useItemsDispatch } from "../../../items/_state/hooks"
import Loader from "../../../loader"

function PaginationLoadMore() {
  const itemsDispatch = useItemsDispatch()
  const itemsState = useItemsState()

  function onViewChange(inView, entry) {
    if (!shopwp.misc.isPro || !itemsState.settings.infiniteScroll) {
      return
    }

    if (inView && !itemsState.isLoading) {
      onClick()
    }
  }

  function onClick() {
    itemsDispatch({
      type: "SET_IS_FETCHING_NEXT",
      payload: true,
    })
    itemsDispatch({ type: "SET_IS_LOADING", payload: true })
  }

  const loadMoreButtonCSS = css`
    max-width: 150px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  `

  return usePortal(
    <InView rootMargin="10px 0px 0px 0px" as="div" onChange={onViewChange}>
      <div
        css={[buttonCSS, loadMoreButtonCSS]}
        disabled={itemsState.isLoading}
        className={"wps-btn-next-page"}
        onClick={onClick}
      >
        {itemsState.isLoading ? (
          <Loader isLoading={itemsState.isLoading} />
        ) : (
          itemsState.settings.paginationLoadMoreText
        )}
      </div>
    </InView>,
    document.querySelector(itemsState.settings.dropzoneLoadMore)
  )
}

export default PaginationLoadMore
