import { post } from '../request'

function getAllTags() {
	return post('products/tags')
}

function getAllVendors() {
	return post('products/vendors')
}

function getAllTypes() {
	return post('products/types')
}

function getProducts(data) {
	return post('query/products', data)
}

function getProductById(data) {
	return post('query/product/id', data)
}

function getProductsByCollections(data) {
	return post('query/products/collections', data)
}

export {
	getAllTags,
	getAllVendors,
	getAllTypes,
	getProducts,
	getProductById,
	getProductsByCollections,
}
