import { rSet } from "Utils"

function ShopReducer(state, action) {
  switch (action.type) {
    case "SET_COUNTRY": {
      return rSet("country", action, state)
    }

    case "SET_LANGUAGE": {
      return rSet("language", action, state)
    }

    case "SET_CURRENCY": {
      return rSet("currency", action, state)
    }

    case "TOGGLE_CART": {
      return rSet("isCartOpen", action, state)
    }

    case "SET_IS_CART_UPDATING": {
      return rSet("isCartUpdating", action, state)
    }

    case "SET_TOTAL_LINE_ITEMS": {
      return rSet("totalLineItems", action, state)
    }

    case "SET_CART_DATA": {
      const updatedCart = rSet("cartData", action, state)

      wp.hooks.doAction("on.cartUpdate", updatedCart)

      return updatedCart
    }

    default: {
      throw new Error(`Unhandled action type: ${action.type} in ShopReducer`)
    }
  }
}

export default ShopReducer
