import isEmpty from 'lodash/isEmpty'

function checkNextPage(items) {
	if (!items || items.length == 0) {
		return false
	}

	return items[items.length - 1].hasNextPage
}

function checkPrevPage(items) {
	if (!items || items.length == 0) {
		return false
	}

	return items[0].hasPreviousPage
}

function checkHasResults(items) {
	return !isEmpty(items)
}

function isHidingPagination(settings, hasMoreItems, isModal) {
	if (shopwp.general.hidePagination) {
		return true
	}

	if (isModal) {
		return true
	}

	if (settings.limit && settings.limit <= settings.pageSize) {
		return true
	}

	if (!hasMoreItems) {
		return true
	}

	return !settings.pagination
}

export { checkNextPage, checkPrevPage, checkHasResults, isHidingPagination }
