/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useDebounce } from "use-debounce"
import { useItemsState, useItemsDispatch } from "../../../items/_state/hooks"
import { useSearchDispatch } from "../../_state/hooks"
import { useFirstRender } from "Hooks"

const SearchIcon = wp.element.lazy(() =>
  import(/* webpackChunkName: 'SearchIcon-public' */ "../../icon")
)

function SearchInput() {
  const { useEffect, useState, Suspense } = wp.element
  const [localTerm, setLocalTerm] = useState(() => "")
  const [debouncedSearchTerm] = useDebounce(localTerm, 350)
  const isFirstRender = useFirstRender()

  const itemsState = useItemsState()
  const itemsDispatch = useItemsDispatch()
  const searchDispatch = useSearchDispatch()

  function setSearchTerm(value) {
    setLocalTerm(value)
  }

  useEffect(() => {
    if (isFirstRender) {
      return
    }

    var newQ = itemsState.settings.searchBy + ":" + debouncedSearchTerm

    if (
      itemsState.settings.searchBy === "title" &&
      !itemsState.settings.searchExactMatch
    ) {
      newQ += "*"
    }

    itemsDispatch({
      type: "SET_QUERY_PARAMS",
      payload: {
        ...itemsState.queryParams,
        query: newQ,
      },
    })

    searchDispatch({ type: "SET_SEARCH_TERM", payload: debouncedSearchTerm })

    if (!debouncedSearchTerm) {
      return
    }

    itemsDispatch({ type: "SET_IS_FETCHING_NEW", payload: true })
  }, [debouncedSearchTerm])

  const spinnerCSS = css`
    position: absolute;
    top: 14px;
    right: 30px;
    font-size: 15px;
  `

  const searchInputCSS = css`
    padding: 1em;
    font-size: 1em;
    border: none;
    border: 1px solid #ddd;
    outline: none;
    width: 100%;
    -webkit-appearance: none;

    &::-webkit-search-cancel-button {
      display: ${itemsState.isLoading ? "none" : "block"};
      &:hover {
        cursor: pointer;
      }
    }
  `
  const searchInputWrapperCSS = css`
    width: 100%;
    position: relative;
  `

  return (
    <Suspense fallback={false}>
      <div className="wps-search-input-wrapper" css={searchInputWrapperCSS}>
        <input
          type="search"
          id="wps-search-input"
          className="wps-search-input"
          name="search"
          role="searchbox"
          val={localTerm}
          placeholder={itemsState.settings.searchPlaceholderText}
          aria-label={itemsState.settings.searchPlaceholderText}
          css={searchInputCSS}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        {!localTerm ? <SearchIcon /> : null}

        {itemsState.isLoading ? (
          <div css={spinnerCSS}>{wp.i18n.__("Loading", "shopwp")} ...</div>
        ) : null}
      </div>
    </Suspense>
  )
}

export default SearchInput
