function CollectionInitialState({ itemsState, payload }) {
	itemsState.settings.products.collection = payload.title
	itemsState.settings.products.query = itemsState.settings.query

	return {
		settings: itemsState.settings,
		payload: payload,
		products: payload.products ? payload.products.edges : false,
		productOptions: {
			id: payload.id + '-products',
			settings: itemsState.settings,
			element: document.querySelector(
				itemsState.settings.dropzoneCollectionProducts
			),
			sortingElement: document.querySelector(
				itemsState.settings.dropzoneCollectionProductsSorting
			),
			queryType: 'collectionProducts',
		},
		productQueryParams: {
			query: itemsState.settings.products.query,
			sortKey: itemsState.settings.products.sortBy,
			reverse: itemsState.settings.products.reverse,
			first: itemsState.settings.products.pageSize,
			collection_titles: itemsState.settings.products.collection,
		},
		notice: false,
		isFetchingNew: false,
		isLoading: false,
		element: false,
		selectedVariant: false,
	}
}

export default CollectionInitialState
