import isEmpty from "lodash/isEmpty"
import has from "lodash/has"

function mergeDefaults(userSettings, defaults) {
  if (has(defaults, "default")) {
    var d = defaults.default
  } else {
    var d = defaults
  }

  if (isEmpty(userSettings)) {
    return d
  } else {
    return {
      ...d,
      ...userSettings,
    }
  }
}

export { mergeDefaults }
