const Notice = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Notice-public' */ "../notice")
)

function ErrorFallback({ error = "" }) {
  return (
    <Notice status="error">
      {error.message
        ? error.message
        : wp.i18n.__(
            "Uh oh something went wrong. Please reload the page and try again. ",
            "shopwp"
          )}
    </Notice>
  )
}

export default ErrorFallback
