import { TranslatorStateContext, TranslatorDispatchContext } from "./context"

function useTranslatorState() {
  const context = wp.element.useContext(TranslatorStateContext)

  if (!context) {
    throw new Error(
      "useTranslatorState must be used within the TranslatorProvider"
    )
  }

  return context
}

function useTranslatorDispatch() {
  const context = wp.element.useContext(TranslatorDispatchContext)

  if (!context) {
    throw new Error(
      "useTranslatorDispatch must be used within the TranslatorProvider"
    )
  }

  return context
}

export { useTranslatorState, useTranslatorDispatch }
