/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useSpring, animated } from "react-spring"

function SlideInFromRight({ children }) {
  const props = useSpring({
    from: {
      opacity: 0,
      transform: "translateX(25px)",
    },
    to: {
      opacity: 1,
      transform: "translateX(0px)",
    },
  })

  return <animated.div style={props}>{children}</animated.div>
}

function SlideInFromRightCart({ children, isOpen, customStyles }) {
  const props = useSpring({
    transform: isOpen ? "translateX(0%)" : "translateX(120%)",
  })

  return (
    <animated.div css={customStyles} style={props}>
      {children}
    </animated.div>
  )
}

function SlideInFromTop({ children, options = {} }) {
  var finalOptions = {
    from: {
      opacity: 0,
      transform: "translateY(-10px)",
    },
    to: {
      opacity: 1,
      transform: "translateY(0px)",
    },
    ...options,
  }

  var props = useSpring(finalOptions)

  return <animated.div style={props}>{children}</animated.div>
}

function SlideInFromBottom({ children, options = {} }) {
  var finalOptions = {
    from: {
      opacity: 0,
      transform: "translateY(10px)",
    },
    to: {
      opacity: 1,
      transform: "translateY(0px)",
    },
    ...options,
  }

  var props = useSpring(finalOptions)

  return <animated.div style={props}>{children}</animated.div>
}

export {
  SlideInFromRight,
  SlideInFromTop,
  SlideInFromBottom,
  SlideInFromRightCart,
}
