import isEmpty from 'lodash/isEmpty'

const isShowingComponent = (settings, type) => {
	if (!settings.excludes || isEmpty(settings.excludes)) {
		return true
	}

	if (
		type === 'description' &&
		shopwp.misc.postID === shopwp.general.pageProducts
	) {
		return shopwp.general.productsPlpDescriptionsToggle
	}

	return !settings.excludes.includes(type)
}

export { isShowingComponent }
