import {
  maybeAlterErrorMessage,
  isWordPressError,
  getWordPressErrorMessage,
} from "../errors"
import to from "await-to-js"
import { getCollections } from "../internal/collections"

function fetchCollections(
  queryParams,
  shopState,
  cursor = false,
  withProducts = false,
  queryParamsProducts = false
) {
  return new Promise(async (resolve, reject) => {
    if (!queryParams) {
      return reject({
        type: "error",
        message: wp.i18n.__(
          "Uh oh, no query parameters were passed for fetchCollections. Please clear your browser cache and try again.",
          "shopwp"
        ),
      })
    }

    var params = {
      queryParams: queryParams,
      shopState: shopState,
      withProducts: withProducts,
      queryParamsProducts: queryParamsProducts,
    }

    if (cursor) {
      params.queryParams.cursor = cursor
    }

    const [resultsError, results] = await to(getCollections(params))

    if (resultsError) {
      return reject({
        type: "error",
        message: maybeAlterErrorMessage(resultsError),
      })
    }

    if (isWordPressError(results)) {
      return reject({
        type: "error",
        message: getWordPressErrorMessage(results),
      })
    }

    resolve(results.data)
  })
}

export { fetchCollections }
