/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { has } from "lodash"
import { useCartState, useCartDispatch } from "../_state/hooks"
import { useShopState, useShopDispatch } from "../../shop/_state/hooks"
import { useAction, useFirstRender } from "Hooks"
import { checkoutRedirect } from "Common/checkout"
import { mq } from "Common/css"
import { SlideInFromRightCart } from "Common/animations"

import {
  addLines,
  removeLines,
  updateAttrs,
  getExistingCart,
  createNewCart,
  directCheckout,
  addDiscount,
  updateCartNote,
} from "../api.jsx"

import CartContainer from "../container"
import CartLoadingContents from "../contents/loading"

function CartWrapper() {
  const { useRef, useState, useEffect } = wp.element
  const cartElement = useRef()
  const isFirstRender = useFirstRender()

  const shopState = useShopState()
  const shopDispatch = useShopDispatch()
  const cartState = useCartState()
  const cartDispatch = useCartDispatch()

  const updateCartAttributes = useAction("do.updateCartAttributes")
  const removeLineItems = useAction("do.removeLineItems")
  const setCartNotes = useAction("do.setCartNote", null)
  const lineItemsAdded = useAction("do.addToCart")
  const discountCode = useAction("do.setCartDiscount", null)
  const doDirectCheckout = useAction("do.directCheckout", null)
  const doCartToggle = useAction("do.cartToggle", null)

  const doCheckout = useAction("do.checkout", null)

  const [cartId] = useState(() => localStorage.getItem("shopwp-cart-id"))

  function openCart() {
    shopDispatch({ type: "TOGGLE_CART", payload: true })
  }

  function closeCart() {
    shopDispatch({ type: "TOGGLE_CART", payload: false })
  }

  /*
	
	Main cart bootstrap hook
	
	*/
  useEffect(() => {
    if (cartId) {
      getExistingCart(cartId, cartState, shopState, cartDispatch, shopDispatch)
    } else {
      createNewCart(cartState, shopState, cartDispatch, shopDispatch)
    }
  }, [])

  useEffect(() => {
    if (doCheckout === null) {
      return
    }

    checkoutRedirect(shopState.cartData.checkoutUrl)
  }, [doCheckout])

  useEffect(() => {
    if (doCartToggle === null) {
      return
    }

    if (doCartToggle === "open") {
      openCart()
    } else if (doCartToggle === "close") {
      closeCart()
    }
  }, [doCartToggle])

  useEffect(() => {
    if (doDirectCheckout === null) {
      return
    }

    directCheckout(doDirectCheckout, cartState, cartDispatch, shopState)
  }, [doDirectCheckout])

  useEffect(() => {
    if (discountCode === null) {
      return
    }

    if (shopwp.misc.isPro && discountCode) {
      addDiscount(cartDispatch, shopState, discountCode, shopDispatch)
    }
  }, [discountCode])

  useEffect(() => {
    if (!updateCartAttributes) {
      return
    }

    updateAttrs(updateCartAttributes, shopState, cartDispatch, shopDispatch)
  }, [updateCartAttributes])

  useEffect(() => {
    if (!removeLineItems) {
      return
    }

    removeLines(removeLineItems, shopState, cartDispatch, shopDispatch)
  }, [removeLineItems])

  useEffect(() => {
    if (setCartNotes === null) {
      return
    }

    updateCartNote(setCartNotes, shopState, cartDispatch, false, shopDispatch)
  }, [setCartNotes])

  // TODO: Remove in 5.0
  function getLegacyLines(lineItemsAdded) {
    const newLines = {}

    newLines.lines = lineItemsAdded.lineItems

    return newLines.lines.map((line) => {
      if (line.variantId === lineItemsAdded.lineItemOptions.variantId) {
        if (
          has(lineItemsAdded.lineItemOptions, "subscription") &&
          lineItemsAdded.lineItemOptions.subscription
        ) {
          line.subscription = {
            sellingPlanId:
              lineItemsAdded.lineItemOptions.subscription.sellingPlanId,
          }
        }
      }

      return line
    })
  }

  // TODO: Remove in 5.0
  function isLegacyParams(lineItemsAdded) {
    return has(lineItemsAdded, "lineItems")
  }

  function addDefaults(lineItemsAdded) {
    return {
      lines: [],
      language: shopState.language,
      country: shopState.country,
      extras: {
        openCartAfterAdding: true,
      },
      ...lineItemsAdded,
    }
  }

  useEffect(() => {
    if (!lineItemsAdded) {
      return
    }

    if (isLegacyParams(lineItemsAdded)) {
      var lines = getLegacyLines(lineItems)
    } else {
      var lines = lineItemsAdded.lines
    }

    var lineItems = addDefaults(lineItemsAdded)

    if (lineItems.extras.openCartAfterAdding) {
      openCart()
    }

    var dataToAdd = {
      cartId: shopState.cartData.id,
      language: lineItems?.language ? lineItems.language : shopState.language,
      country: lineItems?.country ? lineItems.country : shopState.country,
      lines: lines,
    }

    addLines(dataToAdd, cartDispatch, shopDispatch)
  }, [lineItemsAdded])

  const cartCSS = css`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    transition: transform 320ms ease;
    box-sizing: border-box;

    ${mq("xsmall")} {
      width: 100%;
    }

    input[type="number"]::-webkit-inner-spin-button,
    input[type="number"]::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  `

  const cartContainerCSS = css`
    width: 400px;
    padding: 1em;
    position: fixed;
    height: 100%;
    right: 0px;
    top: 0px;
    margin-top: 0px;
    background: white;
    box-shadow: rgb(0 0 0 / 10%) -17px 0px 35px;
    z-index: 99999999;

    ${mq("xsmall")} {
      width: 100%;
    }
  `

  return (
    <SlideInFromRightCart
      isOpen={shopState.isCartOpen}
      customStyles={cartContainerCSS}
    >
      <div ref={cartElement} className="wps-cart" css={cartCSS}>
        <CartLoadingContents
          settings={cartState.settings}
          isUpdating={shopState.isCartUpdating}
        />

        <CartContainer
          isCartOpen={shopState.isCartOpen}
          settings={cartState.settings}
        />
      </div>
    </SlideInFromRightCart>
  )
}

export default CartWrapper
