/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useItemsState, useItemsDispatch } from "../_state/hooks"
import { useShopState } from "../../shop/_state/hooks"
import { useFirstRender, useAction } from "Hooks"
import useIsMounted from "ismounted"

const Notice = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Notice-public' */ "../../notice")
)

import { useGetItemsQuery, useGetTemplateQuery } from "./api"

function Item({ children }) {
  const { useEffect, Suspense } = wp.element
  const itemsState = useItemsState()
  const itemsDispatch = useItemsDispatch()
  const shopState = useShopState()
  const isMounted = useIsMounted()
  const isFirstRender = useFirstRender()
  const doShopHydrate = useAction("do.shopHydrate", null)

  const getTemplateQuery = useGetTemplateQuery(itemsState, itemsDispatch)
  const getItemsQuery = useGetItemsQuery(
    itemsState,
    itemsDispatch,
    shopState,
    isMounted
  )

  useEffect(() => {
    if (isFirstRender || doShopHydrate === null) {
      return
    }

    itemsDispatch({
      type: "SET_IS_FETCHING_NEW",
      payload: true,
    })
  }, [doShopHydrate])

  // These deps don't require a new fetch, but still require a new render when changed.
  useEffect(() => {
    if (itemsState.isLoading || isFirstRender) {
      return
    }

    if (!itemsState.payload) {
      return
    }

    itemsDispatch({
      type: "SET_HAS_NEXT_PAGE",
      payload: itemsState.customHasNextPage,
    })

    if (itemsState.customTotalShown) {
      itemsDispatch({
        type: "SET_TOTAL_SHOWN",
        payload: itemsState.customTotalShown,
      })
    }

    itemsDispatch({
      type: "UPDATE_PAYLOAD",
      payload: {
        items: itemsState.payload,
        replace: false,
      },
    })
  }, [
    itemsState.settings.limit,
    itemsState.customHasNextPage,
    itemsState.customTotalShown,
  ])

  useEffect(() => {
    if (itemsState.isLoading || isFirstRender || !itemsState.customPayload) {
      return
    }

    itemsDispatch({
      type: "UPDATE_PAYLOAD",
      payload: {
        items: itemsState.customPayload,
        replace: true,
      },
    })
  }, [itemsState.customPayload])

  useEffect(() => {
    if (itemsState.isLoading || isFirstRender) {
      return
    }

    if (!itemsState.payload) {
      return
    }

    itemsDispatch({
      type: "SET_SETTINGS",
      payload: itemsState.settings,
    })
  }, [itemsState.settings])

  useEffect(() => {
    if (isFirstRender) {
      return
    }

    itemsDispatch({
      type: "SET_IS_LOADING",
      payload: itemsState.isCustomLoading,
    })
  }, [itemsState.isCustomLoading])

  return itemsState.notice && itemsState.componentType !== "search" ? (
    <Suspense fallback={false}>
      <Notice status={itemsState.notice.type}>
        {itemsState.notice.message}
      </Notice>
    </Suspense>
  ) : (
    children
  )
}

export default Item
