/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { usePortal } from "Hooks"
import { useItemsState } from "../../items/_state/hooks"
import SearchInput from "./input"

function SearchForm() {
  const itemsState = useItemsState()

  const searchWrapperCSS = css`
    max-width: ${itemsState.settings.containerWidth};
    margin: 0 auto;

    .is-loading {
      position: absolute;
      bottom: -30px;
      font-size: 19px;
    }

    .components-notice {
      margin: 0;
      width: 100%;
    }
  `

  const searchInputWrapperCSS = css`
    display: flex;
    margin-bottom: 2em;
  `

  function onSubmit(e) {
    e.preventDefault()
  }

  return usePortal(
    <form
      className="wps-search-form"
      css={searchWrapperCSS}
      onSubmit={onSubmit}
    >
      <div className="wps-search-wrapper" css={searchInputWrapperCSS}>
        <SearchInput />
      </div>
    </form>,
    document.querySelector(itemsState.settings.dropzoneForm)
  )
}

export default wp.element.memo(SearchForm)
