import TranslatorReducer from "./reducer"
import TranslatorInitialState from "./initial-state"
import { TranslatorStateContext, TranslatorDispatchContext } from "./context"

function TranslatorProvider(props) {
  const [state, dispatch] = wp.element.useReducer(
    TranslatorReducer,
    TranslatorInitialState(props.options)
  )

  return (
    <TranslatorStateContext.Provider value={state}>
      <TranslatorDispatchContext.Provider value={dispatch}>
        {props.children}
      </TranslatorDispatchContext.Provider>
    </TranslatorStateContext.Provider>
  )
}

export default TranslatorProvider
